<template>
  <Header />
  <section>
    <div class="container">
      <div class="row">
        <div class="col-sm-3">
          <div class="left-sidebar">
            <h2>الأقسام</h2>
            <div class="panel-group category-products" id="accordian">
              <!--category-productsr-->

              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('اكسسوريز')">اكسسوريز</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('عنايه')">عنايه</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('مكياج')">مكياج</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('شنط')">شنط</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('عطور')">عطور</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('اجهزه')">اجهزه</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('ملابس نساء')">ملابس نساء</router-link>
                  </h4>
                </div>
              </div>
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h4 class="panel-title">
                    <router-link to="#" @click="option('رجال')">رجال</router-link>
                  </h4>
                </div>
              </div>
            </div>
            <!--/category-products-->
          </div>
        </div>

        <div class="col-sm-9 padding-right">
          <div class="features_items">
            <!--features_items-->
            <h2 class="title text-center">{{ categroy }}</h2>
            <div v-for="(item, index) in this.data" :key="index" class="col-sm-4">
              <div class="product-image-wrapper" v-if="categroy == 'المنتجات' || categroy === item.categroy">
                <div class="single-products">
                  <div class="productinfo text-center">
                    <img src="images/products/2.jpeg" alt="" />
                    <h2>{{ $filters.USD(item.price) }}</h2>
                    <p> {{ item.title }}</p>
                    <router-link to="#" class="btn btn-default add-to-cart"><i class="fa fa-shopping-cart"></i>أضف إلى السلة</router-link>
                  </div>
                  <div class="product-overlay">
                    <div class="overlay-content">
                      <h2>{{ $filters.USD(item.price) }}</h2>
                      <p>{{ item.title }}</p>
                      <router-link to="#" class="btn btn-default add-to-cart"><i class="fa fa-shopping-cart"></i>أضف إلى
                        السلة</router-link>
                    </div>
                  </div>
                </div>
                <div class="choose">
                  <ul class="nav nav-pills nav-justified">
                    <li>
                      <router-link to="#" @click="addToFavorite(index)"><i class="fa fa-plus-square"></i>أضف إلى المفضلة</router-link>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <!-- <p v-for="item, index in search" :key="index"> {{ item }}</p> -->
          </div>
          <!--features_items-->
        </div>
      </div>
    </div>
  </section>
  <Footer />
</template>
<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "shop ",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      data: [],
      categroy: "المنتجات",

    }
  },
  methods: {
    option(val) {
      this.categroy = val
    },
    addToFavorite(x) {
      
      this.$store.state.fav.push(this.data[x]);
      localStorage.fav = JSON.stringify(this.$store.state.fav);
    },

  },
  created() {
    if (localStorage.data) {
      this.data = JSON.parse(localStorage.data);
    }
  },

  
};
</script>