<template>
  <Header />
  <section id="form">
    <!--form-->
    <div class="container">
      <div class="row">
        <!--login form-->
        <div class="col-sm-6 col-sm-offset-1">
          <div class="login-form">
            <h2>التسجيل باستخدام حسابك</h2>
            <form>
              <input type="email" v-model="email" placeholder="عنوان البريد الإلكتروني الخاص بك" />
              <input type="password" v-model="password" placeholder="كلمة المرور" />
              <!-- <router-link to="/login"><button type="submit">Login</button></router-link> -->
              <button type="button"    @click.prevent="login()" class="btn btn-default">
                تسجيل الدخول
              </button>
              <br />
              <h4>
                <router-link to="/register">أو التسجيل كمستخدم جديد</router-link>
              </h4>
            </form>
          </div>
        </div>
        <!--/login form-->
      </div>
    </div>
  </section>
  <!--/form-->
  <Footer />
</template>

<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import axios from "axios";
import store from "@/store/index.js";
import router from "@/router";
import { isValid } from "ipaddr.js";
export default {
  name: "login",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      email: "",
      password: "",
    }

  },
  methods: {
    login() {
      axios.post("http://127.0.0.1:8000/api/login", {
        email: this.email,
        password: this.password,
      },
        {
          headers: {
            "Content-Type": "application/json",
            "Authorization": "Bearer " + store.token,
          }
        }
      ).then(function (response) {
      //  window.location.href = "http://localhost:8080/";
       store.token = response.data.token;
        console.log(store.token);
      });
      router.push('/');
    }
  }
};
</script>