<template>
  <Header />
  <div id="contact-page" class="container">
    <div class="bg">
      <div class="row" style="margin-bottom: 50px">
        <div class="col-sm-12">
          <h2 class="title text-center">إضافة منتج جديد</h2>
          <!--<div id="gmap" class="contact-map">
					</div>-->
        </div>
      </div>
      <div class="row">
        <div class="col-sm-8">
          <div class="contact-form">
            <div class="status alert alert-success" style="display: none"></div>
            <form id="main-contact-form" class="contact-form row" name="contact-form" method="post">
              <div class="form-group col-md-6">
                <input v-model="title" type="text" name="name" class="form-control" required="required"
                  placeholder="اسم المنتج" />
              </div>
              <div class="form-group col-md-6">
                <input v-model="price" type="number" name="price" class="form-control" required="required"
                  placeholder="السعر" />
              </div>
              <div class="form-group col-md-6">
                <select name="subject" class="form-control" v-model="categroy">
                  <option>اكسسوريز</option>
                  <option>عنايه</option>
                  <option>مكياج</option>
                  <option>شنط</option>
                  <option>عطور</option>
                  <option>اجهزه</option>
                  <option>ملابس نساء</option>
                  <option>رجال</option>
                </select>
              </div>
              <div class="form-group col-md-6">
                <input type="file" name="image" class="form-control" required="required" placeholder="اختر صورة المنتج" />
              </div>
              <div class="form-group col-md-12">
                <textarea v-model="desc" name="message" id="message" required="required" class="form-control" rows="8"
                  placeholder="وصف المنتج أو نبذة عنه"></textarea>
              </div>
              <div class="form-group col-md-12">
                <!-- <input @click="edit()" type="submit" name="submit" class="btn btn-primary pull-right" value="حفظ التعديلات" /> -->
                <!-- نوع الانبوت بوتون-->
                <input @click="edit()" type="button" name="submit" class="btn btn-primary pull-right"
                  value="حفظ التعديلات" />

              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!--/#contact-page-->
  <Footer />
</template>
<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import router from "@/router";
import axios from "axios";
export default {
  name: "update",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
    title: "",
      price: "",
      categroy: "",
      date: new Date().toLocaleString(),
      desc: "",
      data:[]
    }
    
  }, 
  methods: {
    edit() {
      axios.put("http://127.0.0.1:8000/api/add/"+router.id, {
        title: this.title,
        price: this.price,
        desc: this.desc,
        categroy: this.categroy
      },
       )
        .then(function (response) {
          console.log(response.data);


          router.push('/control');
          localStorage.setItem( JSON.stringify(this.$store.state.data));

        })
        .catch(function (error) {
          console.log(error);
        });

      // localStorage.dataducts = JSON.stringify(this.dataducts);
    },


  },
  mounted() {
      axios.get(
        "http://127.0.0.1:8000/api/d"+router.id
      ).then( (response)=> {
        this.data=response.data;
       
        router.push('/control');
      })}
      
};
</script>