<template>
  <Header />
  <section id="cart_items">
    <div class="container">
      <div class="breadcrumbs">
        <ol class="breadcrumb">
          <li><router-link to="/">الرئيسية</router-link></li>
          <li class="active">لوحة التحكم</li>
        </ol>
      </div>

      <h4 style="margin-bottom: 3rem">
        <router-link to="/add-new">اضافة منتج جديد</router-link>
      </h4>

      <div class="table-responsive cart_info">
        <table class="table table-condensed">
          <thead>
            <tr class="cart_menu">
              <td class="image">المنتج</td>
              <td class="description"></td>
              <td class="description">الصنف</td>
              <td class="price">السعر</td>
              <td class="price">تاريخ الاضافة</td>
              <td class="total">العمليات</td>
              <td></td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in this.data" :key="item.id">
              <td class="cart_product">
                <a href=""><img src="images/products/8.jpeg" :style="{ 'width': width }" alt="" /></a>
              </td>
              <td class="cart_description">
                <h4>
                  <a href="">{{ item.title }}</a>
                </h4>
              </td>
              <td class="cart_description">
                <h4>
                  <a href="">{{ item.categroy }}</a>
                </h4>
              </td>
              <td class="cart_price">
                <p>{{ $filters.USD(item.price) }}</p>
              </td>
              <td class="cart_description">
                <p>{{ item.date }}</p>
              </td>
              <td class="cart_delete">
                <router-link  class="cart_quantity_delete" to="#"><i @click="deleteitem(item.id)" class="fa fa-times"></i></router-link>
                <router-link class="cart_quantity_delete" to="/update"><i @click="get_id(index)"
                    class="fa fa-pencil-square-o"></i></router-link>
                <router-link class="cart_quantity_delete" to="/details"><i @click="get_id(index)"
                    class="fa fa-info-circle"></i></router-link>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </section>
  <!--/#cart_items-->
  <Footer />
</template>
<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import axios from "axios";

export default {
  name: "control",
  data() {
    return {
      width: "70px",
      data:[]
    }
  },
  components: {
    Header,
    Footer,
  },
  
    created() {
      axios.get(
        "http://127.0.0.1:8000/api/d"
      ).then( (response)=> {
        this.data=response.data;
        var data =JSON.stringify(response.data);

        localStorage.setItem('data', data);


      })
    },
    deleteitem(id) {
      axios.delete("http://127.0.0.1:8000/api/del/"+id).then(function(response){
        this.data=response.data+id;
      })
    },
    get_id(index) {
      localStorage.setItem('item_id', JSON.stringify(index));
    },
  
};
</script> 
